
import { defineComponent } from "vue";

export default defineComponent({
  // props: ['value', 'label', 'type'],
  props: {
    value: {
      type: String,
      required: true,
    },
    label: {
      type: String,
      required: true,
    },
    type: {
      type: String,
      required: true,
    },
  },
  emits: ["update:value"],
  methods: {
    inputHandler(event: Event) {
      return (event.target as HTMLInputElement).value;
    },
  },
});
